.card-small{
    /* border: 1px solid red; */
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 1em;
    background: var(--card-gray);
    border-radius: 8px;
    padding: 1.7em;
  }
  
  .card-small p {
    margin: 0;
  }
  
  .card-small-views{
    /* border: 1px solid green; */
    color: var(--cardTitle);
    font-weight: bold;
    font-size: .9em;
  }
  .card-small-icon{
    /* border: 1px solid orange; */
    text-align: right;
  }
  .card-small-number{
    /* border: solid purple; */
    font-weight: bold;
    font-size: 2.5em;
    line-height: .9;
  }
  .card-small-porcentage{
    /* border: 1px solid blue; */
    text-align: right;
    display: flex;
    align-items: flex-end;
    color: var(--Lime-green);
    justify-content: flex-end;
    font-size: .8em;
  }
  
  .card-small-porcentage.is-danger {
    color: var(--Bright-Red);
  }
  
  .card-small-porcentage span {
    /* border: 1px solid red; */
    display: flex;
    align-items: center;
  }
  
  .card-small-porcentage img {
    margin-right: 5px;
  }
  
  