
/* card */
.card {
  border-radius: 8px;
  overflow: hidden;
  background: var(--card-gray);
  text-align: center;
  padding: 2em 0;
  position: relative;
}

.card-title{
  /* border: 1px solid purple; */
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--cardTitle);
  font-size: .9em;
  font-weight: bold;

}

.card-title img{
  margin-right: 5px;
}

.card:before{
  content: "";
  height: 2px;
  display: block;
  position:absolute;
  left: 0;
  right: 0;
  top: 0;
  
}

.card.facebook::before{
  background:var(--Facebook);
  padding:0.85px;
}
.card.twitter::before{
  background:var(--Twitter);
  padding:0.85px;
  
}
.card.instagram::before{
  background-image: linear-gradient(to right, var(--Instagram-1) 0%, var(--Instagram-2) 100%);
  padding:0.85px;
  
}
.card.youtube::before{
  background:var(--Youtube);
  padding:0.85px;
  
}

.card-followers {
  /* border: 1px solid green; */
}
.card-followers-number {
  /* border: 1px solid red; */
  font-size: 50px;
  font-weight: 700;
}
.card-followers-title {
  /* border: 1px solid blue; */
  display: block;
  text-transform: uppercase;
  color: var(--cardTitle);
  letter-spacing: 5px;
  font-size: .75em;
}

.card-today{
  margin-top: 20px;
  color: var(--Lime-green);
  font-size: .7em;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;

}

.card-today img {
  margin-right: 5px;
}